import React from 'react';
import styles from './header.css';
import Button from '../buttons/button.jsx';
import {connect} from 'react-redux';
import { bindActionCreators } from 'redux';
import {addBook,invalid_book} from '../../actions';
import {validate_date,empty_string,hasBook}from '../../functions/prepare_data.js';




class Header extends React.PureComponent {

    constructor(){
        super();

        this.state={
            title:'',
            date:'',
            author:''
        }
        this.error_code = 0;
        this.error_desc = '';
    }

    handleTitleChange=(e)=>{
        this.setState({
            title: e.target.value
        })
    }

    handleDateChange=(e)=>{
        this.setState({
            date: e.target.value
        })
    }

    handleAuthorChange=(e)=>{
        this.setState({
            author: e.target.value
        })
    }

    onAdd=()=>{
        let data = this.state;
        this.error_code = 0;
        this.error_desc = '';
        if(empty_string(data.title)){
            this.error_code = 1;
            this.error_desc = 'title is empty'
        }
        else if(!validate_date(data.date)){
           
            this.error_code = 2;
            this.error_desc = "date format is wrong";
        }
        else if(empty_string(data.author)){
           
            this.error_code = 3;
            this.error_desc= "author is empty";
        }

        if(!hasBook(data.title,this.props.books)){
            this.error_code = 4;
            this.error_desc= "book exist!!!!";
        }

        if(this.error_code === 0){
            let data = {
                id: Date.now(),
                title: this.state.title,
                date: this.state.date,
                author: this.state.author
            }
            this.props.addBook(data);
            this.setState({
                title:'',
                date:'',
                author:''
            })
        }else{
            this.props.invalid_book(this.error_desc);
        }
    }

    render() {
        return (
           <div className={styles.container}>
                <div className={styles.title}>ADD NEW BOOK!!!</div>
                <div className={styles.content}>
                    <form className={styles.form}>
                        <div className={styles.inputs}>
                            <label>Author: </label>
                            <input type="text" ref="author" onChange={this.handleAuthorChange} placeholder="author" value={this.state.author} />
                        </div>
                        <div className={styles.inputs}>
                            <label>Date: </label>
                            <input type="text" ref="date" onChange={this.handleDateChange} placeholder="date" value={this.state.date}  />
                        </div>
                        <div className={styles.inputs}>
                            <label>Title: </label>
                            <input type="text" ref="title" onChange={this.handleTitleChange} placeholder="title" value={this.state.title} />
                        </div>
                        <div className={styles.inputs + " " + styles.btn}>
                            <Button text={'add'} onClick={this.onAdd} />
                            </div>
                    </form>
                </div>
           </div>
        );
     }
}

const mapStateToProps = (state,own_props) => {
    return{
       books: state.books.books
    }
  };
  
  const mapDispatchToProps = dispatch => {
      return {
        ...bindActionCreators({ addBook,invalid_book }, dispatch)
      }
  };


export default connect(mapStateToProps,mapDispatchToProps)(Header);


