import { FETCH_DATA_SUCCESS, DELETE_BOOK, SAVE_EDIT, ADD_BOOK } from "../actionTypes";

const initialState={
}


export default function reducer(state = initialState,action){
    switch(action.type){
        case FETCH_DATA_SUCCESS: 
            let books = Object.assign({},action.payload);
                return {
                    ...state,
                    books
                }

        case DELETE_BOOK:
            let old_state = state.books.books;
            let new_obj={
                books:[]
            }
            for(let el of old_state){
                if(el.id !== action.payload){
                    new_obj.books.push(el);
                }
            }
            return{
                ...state,
                books: new_obj
            }
        case SAVE_EDIT:
            let olds_state = state.books.books;
            let news_obj={
                books:[]
            }
            if(olds_state !== undefined){
                for(let el of olds_state){
                    if(el.id === action.payload.id){
                        el.title = action.payload.data.title;
                        el.date = action.payload.data.date;
                        el.author = action.payload.data.author;
                    }
                        news_obj.books.push(el);
                }
            }

            return{
                ...state,
                books:news_obj
            }
        case ADD_BOOK:
            let new_book_array = {
                books: state.books.books
            }
            new_book_array.books.push(action.payload.data)

            return {
                ...state,
                books:new_book_array
            }
       default:
       return state;
    }
}


