import React from 'react';
import ReactDOM from 'react-dom';
import App from './App.jsx';
import { createStore, applyMiddleware } from 'redux';
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';
import coreMiddleware from './middleware/core.js';
import reducer from './reducers';
import logger from './middleware/customLogger';
import styles from './global/reset.css';
//pages
import NotFound from './pages/notFound.jsx';

import { Router, Route, browserHistory } from 'react-router';
import { syncHistoryWithStore } from 'react-router-redux';
import registerServiceWorker from './registerServiceWorker';

let store = null;
if(process.env.NODE_ENV == 'development'){
 store = createStore(reducer, applyMiddleware(thunk,logger,coreMiddleware));
}else{
 store = createStore(reducer, applyMiddleware(thunk,coreMiddleware));
}


const history = syncHistoryWithStore(browserHistory, store);



ReactDOM.render(
<Provider store={store}>    
    <Router history={history}>
      <Route path="/" component={App} />   
      <Route path="*" component={NotFound} />
    </Router>
</Provider>
,document.getElementById('root'));
registerServiceWorker();



