import { OPEN_EDITOR,CANCEL_EDIT,SAVE_EDIT } from "../actionTypes";

const initialState={
    id:0,
    open_state: false
}


export default function reducer(state = initialState,action){
    switch(action.type){
        case OPEN_EDITOR:
        return {
            ...state,
            id:action.payload.id,
            open_state: action.payload.open_state
        }
        case CANCEL_EDIT:
        return{
            ...state,
            open_state:action.payload.open_state
        }
        case SAVE_EDIT:
        return{
            ...state,
            open_state:false
        }
        default:
        return state;
    }
}


