export const validate_date=(val)=>{
    let re = /^(\d{1,2})\/(\d{1,2})\/(\d{4})$/;

    if(val !== '') {
      if(val = val.match(re)) {
        if(val[1] < 1 || val[1] > 31) {
          return false;
        }
        if(val[2] < 1 || val[2] > 12) {
          return false;
        }
        if(val[3] < 1902 || val[3] > (new Date()).getFullYear()) {
          return false;
        }
      } else {
        return false;
      }
      return true;
    }
}

export const empty_string=(val)=>{
    if(val === ''){
      return true;
    }
    return false;
}

export const hasBook=(val,books)=>{
  for(let el of books.books){
    if(el.title.toLowerCase() === val.toLowerCase()){
      return false;
    }
  }
  return true;
}



