import { FETCH_DATA_SUCCESS, FETCH_DATA_ERROR, SEND_REQUIEST } from "../actionTypes";
import axios from 'axios';

const coreMiddleware = store => next => action => {
    
    switch(action.type){
        case SEND_REQUIEST:
        axios.get('db.json').then(function(res){
            store.dispatch({type:FETCH_DATA_SUCCESS,payload:res.data});
        }).catch(function(err){
            store.dispatch({type:FETCH_DATA_ERROR, payload:{}})
            return false;
        });
        break;
        default:
        break;
    }

    next(action);
}

export default coreMiddleware;


