import { DELETE_BOOK,CLOSE_NOTIFICATION, ERROR_ADD_BOOK } from "../actionTypes";

const initialState={
    open:false,
    reason: ""
}


export default function reducer(state = initialState,action){
    switch(action.type){
        case DELETE_BOOK:
        return{
            ...state,
            open: true,
            reason: "deleted book!!!"
        }
        
        case CLOSE_NOTIFICATION:
        return {
            ...state,
            open: action.payload.data
        }
        case ERROR_ADD_BOOK:
        return{
            ...state,
            open: true,
            reason: action.payload.text
        }
        default:
        return state;
    }
}


