import React from 'react';
import styles from './book.css';
import Button from '../buttons/button.jsx';
import {connect} from 'react-redux';
import {deleteBook, openEditior} from '../../actions';
import { bindActionCreators } from 'redux';

class Book extends React.Component{
    
    onDelete=()=>{
        this.props.deleteBook(this.props.id);
    }
    onEdit=()=>{
        this.props.openEditior(this.props.id,true);
    }

    render(){
        return(
            <div className={styles.container}>
            <div className={styles.left_side}>
                <div className={styles.image}>

                    <img className={styles.img} src={"../../assets/"+this.props.id+".jpg"} alt="missing image"  />


                </div>
            </div>
                <div className={styles.right_side}>
                    <div className={styles.info}>
                        <h2>Author: {this.props.author}</h2>
                        <h2>Title: {this.props.title}</h2>
                        <h2>Date: {this.props.date}</h2>
                    </div>
                    <div className={styles.button_area}>
                        <Button id={this.props.id} text={'edit'} onClick={this.onEdit} />
                        <Button id={this.props.id} text={'delete'} onClick={this.onDelete} />
                    </div>                    
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state,own_props) => {
    return{
       books: state.books.books
    }
  };
  
  const mapDispatchToProps = dispatch => {
      return {
        ...bindActionCreators({ deleteBook, openEditior }, dispatch)
      }
  };

export default connect(mapStateToProps,mapDispatchToProps)(Book);


