import {DELETE_BOOK,OPEN_EDITOR,CANCEL_EDIT,SAVE_EDIT,ADD_BOOK, CLOSE_NOTIFICATION,ERROR_ADD_BOOK} from '../actionTypes';

//delete book
export const deleteBook =(data)=> dispatch=>{
    dispatch({
        type: DELETE_BOOK,
        payload: data
    });
}

export const openEditior = (id,state) => dispatch=>{
    let data={
        "id": id,
        open_state: state
    }
    dispatch({
        type: OPEN_EDITOR,
        payload: data
    });
}

export const cancelEdit = () => dispatch=>{
    dispatch({
        type: CANCEL_EDIT,
        payload: {
            open_state:false
        }
    });
}

export const savelEdit = (id,data) => dispatch=>{
    dispatch({
        type: SAVE_EDIT,
        payload: {
            id,
            data
        }
    });
}

export const addBook = (data) => dispatch=>{
    dispatch({
        type: ADD_BOOK,
        payload: {
            data
        }
    });
}

export const closeNotifications = () => dispatch=>{
    dispatch({
        type: CLOSE_NOTIFICATION,
        payload: {
            data: false
        }
    });
}

export const invalid_book =(data)=>dispatch=>{
    dispatch({
        type: ERROR_ADD_BOOK,
        payload:{
            text:data
        }
    })
}



