import React from 'react';
import styles from './App.css';
import {connect} from 'react-redux';
import Header from './components/header/header.jsx';
import {SEND_REQUIEST} from './actionTypes/index.js';

import Book from './components/book/book.jsx';
import Editor from './components/editor/editor.jsx';
import Notification from './components/notification/notification.jsx';



class App extends React.Component {
  
  componentDidMount(){
    this.props.onGetBooks();
  }

  showBooks(){
    if(this.props.books !== undefined){
      this.book_length = this.props.books.books.length;
      let bks = [];

       for(let el of this.props.books.books){
        bks.push(
          <Book key={el.id}
                author={el.author}
                id={el.id}
                title={el.title}
                date={el.date} />
        )
       }
       return bks;
    }else {
      return <div>No Books!!!</div>
    }
    
  }

  openEditor(){
    let book = undefined;
    if(this.props.editor.open_state){
      for(let bk of this.props.books.books){
        if(bk.id === this.props.editor.id){
          book = bk;
          break;
        }
      }
      return(
      
        <Editor id={book.id} title={book.title} author={book.author} date={book.date} />
      )
    }else{
      return "";
    }
  }

  openNotification(){
    if(this.props.notification.open){
      return (<Notification text={this.props.notification.reason} />)
    }else{
      return <div />
    }
  }

    render() {
        return (
          <div className={styles.container}>
            <div className={styles.header_container}>
              <Header />
            </div>
            <div className={styles.main_container}>
                {(this.props.editor.open_state)?this.openEditor():this.showBooks()}
            </div>
            {this.openNotification()}
         </div>

        );
     }
}

const mapStateToProps = (state,own_props) => {
  return{
     books: state.books.books,
     editor: state.editor,
     notification: state.notification
  }
};

const mapDispatchToProps = dispatch => {
    return {
      onGetBooks:()=>{
          dispatch({type:SEND_REQUIEST,payload:{}});
      }
    }
};


export default connect(mapStateToProps,mapDispatchToProps)(App);


