import React from 'react';
import styles from './editor.css';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import Button from '../buttons/button.jsx';
import { bindActionCreators } from 'redux';
import { savelEdit,cancelEdit} from '../../actions';
import {validate_date,empty_string}from '../../functions/prepare_data.js';


class Editor extends React.Component{
    static defaultProps = {
        title: 'title',
        date: '10/10/1980',
        id:'1',
        author: 'xxx',
      }


    constructor(props){
        super(props);

        this.state={
            title: props.title,
            date:props.date,
            author:props.author,
            error_desc: '',
            id: props.id
        }
        this.error_code = 0;
    }

    onCancel=()=>{
        this.props.cancelEdit();
    }



    onsavelEdit=()=>{
        let data = this.state;
        this.error_code = 0;
        if(empty_string(data.title)){
            this.setState({
                ...this.state,
                error_desc: 'title is empty'
            })
            this.error_code = 1;
        }
        else if(!validate_date(data.date)){
            this.setState({
                ...this.state,
                error_desc: "date format is wrong"
            })
            this.error_code = 2;
        }
        else if(empty_string(data.author)){
            this.setState({
                ...this.state,
                error_desc: "author is empty"
            })
            this.error_code = 3;
        }

        if(this.error_code === 0){
            this.props.savelEdit(this.state.id,data);
        }
    }

    handleTitleChange=(e)=>{
        this.setState({
            title: e.target.value.replace(/[^a-zA-Z ]/g, "")
        })
    }

    handleDateChange=(e)=>{
        this.setState({
            date: e.target.value
        })
    }

    handleAuthorChange=(e)=>{
        this.setState({
            author: e.target.value.replace(/[^a-zA-Z ]/g, "")
        })
    }


    render(){

            return(
                <div className={styles.container}>
                    <div className={styles.left_side}>
                        <img src={"../../assets/"+this.props.id+".jpg"}/>
                    </div>
                    <div className={styles.right_side}>
                        <form>
                            {(this.error_code !== 0)?<div className={styles.error}>{this.state.error_desc}</div>:""}

                            <label>Title:</label><input type="text" ref="title" onChange={this.handleTitleChange} className={styles.inputes} value={this.state.title} />
                            <label>date:</label><input type="text" ref="date" onChange={this.handleDateChange} className={styles.inputes} value={this.state.date}  />
                            <label>Author:</label><input type="text" ref="author" onChange={this.handleAuthorChange} className={styles.inputes} value={this.state.author} />                            
                        </form>
                        <div className={styles.button_area}>
                            <Button id={this.props.id} text={'save'} onClick={this.onsavelEdit}/>
                            <Button id={this.props.id} text={'cancel'} onClick={this.onCancel}/>
                        </div>
                    </div>
                </div>
            )
    }
}

Editor.propTypes = {
    title: PropTypes.string.isRequired,
    date: PropTypes.string.isRequired,
    author: PropTypes.string.isRequired
}

const mapStateToProps = (state,own_props) => {
    return{
       books: state.books.books
    }
  };
  
  const mapDispatchToProps = dispatch => {
      return {
        ...bindActionCreators({ savelEdit,cancelEdit }, dispatch)
      }
  };

export default connect(mapStateToProps,mapDispatchToProps)(Editor);


