import React from 'react';

class NotFound extends React.Component{
    render(){
        return(
            <div style={{backgroundColor:"#e2dddd", width:"100vw",height:"100vh"}}>
                <span style={{color:"22px",position:"absolute",top:"50%",left:"50%",transform:"translate(-50%,-50%)", fontSize:"50px",fontWeight:"bold"}}>
                   NOT FOUND PAGE!!!!!
                </span>
            </div>
        )
    }
}

export default NotFound;


